/*
 * 按行截断 & 行分割线样式
 * 1~5行截断，其中单行截断采用强制不换行方式实现
 * @ BUG
 * 多行截断box-orient:vertical;当超行截断时，末行line-height与box-height距离缺失
 * 目前给所有多行截断样式增加margin-bottom值补足行距，但会导致文字不足未发生截断时过多补足(目前无解)
 * @author yangfan
 */

.c-line-clamp2,
.c-line-clamp3,
.c-line-clamp4,
.c-line-clamp5 {
    display: -webkit-box;
    overflow: hidden;
    margin-bottom: .05rem;
    white-space: normal;
    text-overflow: ellipsis;
    -webkit-box-orient: vertical;
}

.c-line-clamp2 {
    -webkit-line-clamp: 2;
}

.c-line-clamp3 {
    -webkit-line-clamp: 3;
}

.c-line-clamp4 {
    -webkit-line-clamp: 4;
}

.c-line-clamp5 {
    -webkit-line-clamp: 5;
}

.c-line-clamp1 {
    display: block;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.c-line-top {
    border-top: 1px solid #eee;
}

.c-line-dotted-top {
    border-top: 1px dotted #eee;
}

.c-line-bottom {
    border-bottom: 1px solid #eee;
}

.c-line-dotted-bottom {
    border-bottom: 1px dotted #eee;
}


