/*
 * loading动画组件样式
 * 调用：
 * <div class="c-loading">
 *     <i class="c-icon">&#xe780</i>
 *     <p>加载中…</p>
 * </div>
 * 使用手百loading icon的动画组件样式
 * 调用：
 * <div class="c-loading-zbios">
 *     <i class="c-icon">&#xe65d</i>
 *     <p>加载中…</p>
 * </div>

 * @author yangfan
 */

@-webkit-keyframes c-loading-rotation {
    from {
        -webkit-transform: rotate(1deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

.c-loading,
.c-loading-zbios {
    text-align: center;
}

.c-loading i {
    position: relative;
    display: block;
    width: .54rem;
    height: .54rem;
    margin: auto;
    font-size: .3rem;
    line-height: .52rem;
    color: #f3f3f3;
}

.c-loading i::before {
    content: "";
    position: absolute;
    display: block;
    width: .5rem;
    height: .5rem;
    margin: auto;
    border: .02rem solid #f3f3f3;
    border-top-color: #ddd;
    border-radius: 50%;
    -webkit-transform-origin: 50% 50%;
    -webkit-animation: c-loading-rotation 1s ease 0s infinite normal;
}

.c-loading-zbios i {
    position: relative;
    display: block;
    width: .54rem;
    height: .54rem;
    margin: auto;
    font-size: .48rem;
    line-height: .54rem;
    color: #f3f3f3;
    -webkit-transform-origin: 50% 50%;
    -webkit-animation: c-loading-rotation .5s linear 0s infinite normal;
}

.c-loading p,
.c-loading-zbios p {
    margin-top: .08rem;
    text-indent: .5em;
    color: #999;
}


