/*
 * 滑动组件样式
 * @author yangfan
 */

.c-scroll-wrapper,
.c-scroll-wrapper-new {
    position: relative;
    overflow: hidden;
}

.c-scroll-wrapper-new .c-scroll-touch {
    padding-right: .17rem;
    padding-left: .17rem;
}

.c-scroll-parent-gap {
    padding: 0 .11rem 0 .17rem;
}

.c-scroll-parent-gap .c-scroll-element-gap {
    padding-right: .1rem;
}

.c-scroll-indicator-wrapper {
    height: 6px;
    text-align: center;
}

.c-scroll-indicator-wrapper .c-scroll-indicator {
    vertical-align: top;
}

.c-scroll-indicator {
    position: relative;
    display: inline-block;
    height: 6px;
}

.c-scroll-indicator .c-scroll-dotty {
    position: absolute;
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: #999;
}

.c-scroll-indicator .c-scroll-dotty-now {
    background-color: #999;
}

.c-scroll-indicator span {
    display: block;
    float: left;
    width: 6px;
    height: 6px;
    margin-right: .07rem;
    border-radius: 50%;
    background-color: #e1e1e1;
}

.c-scroll-indicator span:last-child {
    margin-right: 0;
}

.c-scroll-touch {
    position: relative;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
    margin-top: -.3rem;
    padding-bottom: .3rem;
    -webkit-transform: translateY(.3rem);
    transform: translateY(.3rem);
}


/*
 * 定位组件样式
 * @author xieyaowu
 */

.c-location-wrap {
    overflow: hidden;
    padding: 0 .15rem;
    background-color: #f7f7f7;
}

.c-location-header-tips {
    font-size: .13rem;
}

.c-location-header-btn {
    padding-top: .08rem;
    -webkit-box-flex: 0;
    -webkit-flex: none;
}

.c-location-header-btn div {
    display: inline-block;
}

.c-location-header-btn-reload:after {
    content: "";
    display: inline-block;
    overflow: hidden;
    width: 1px;
    height: .1rem;
    margin: 0 .08rem;
    background-color: #ccc;
}

.c-location-header-btn-788 {
    display: none;
}

.c-location-header-btn-in,
.c-location-header-btn-reload {
    color: #333;
}

.c-location-header-btn .c-icon {
    vertical-align: top;
    color: #666;
}

.c-location-header-tips {
    color: #999;
}


/*上面是全局加载的*/

.c-location-header-tips-err {
    color: #c00;
}

.c-location-header-tips-success {
    color: #38f;
}

.c-location-header-btn-reload-ing .c-location-header-btn-787 {
    display: none;
}

.c-location-header-btn-reload-ing .c-location-header-btn-788 {
    display: inline-block;
    color: #999;
    -webkit-animation-name: c_location_rotate;
    -webkit-animation-duration: 1.5s;
    -webkit-animation-timing-function: linear;
    -webkit-animation-iteration-count: infinite;
}

.c-location-header-btn-reload-ing {
    color: #999;
}

@-webkit-keyframes c_location_rotate {
    from {
        -webkit-transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
    }
}

@keyframes c_location_rotate {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}

.c-location-header-btn-in-active,
.c-location-header-btn-in-active .c-icon {
    color: #38f;
}

.c-location-form {
    position: relative;
}

.c-location-form .c-input {
    padding-right: .7rem;
}

.c-location-input-close {
    position: absolute;
    z-index: 10;
    top: 1px;
    right: .37rem;
    display: none;
    width: .36rem;
    height: .36rem;
    font-size: .16rem;
    line-height: .36rem;
    text-align: center;
    color: #ddd;
}

.c-location-form .c-input:focus {
    border-color: #ddd #eee #eee #ddd;
    background-color: #fff;
}

.c-location-sub {
    position: absolute;
    z-index: 10;
    top: 1px;
    right: 1px;
    width: .36rem;
    height: .36rem;
    border-left: 1px solid #eee;
    line-height: .36rem;
    text-align: center;
    background-color: #fafafa;
}

.c-location-body {
    display: none;
    padding-bottom: .14rem;
}

.c-location-down {
    display: none;
    border: 1px solid #eee;
    border-top: none;
    background-color: #fff;
    -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

.c-location-down-tips {
    height: .38rem;
    padding-left: .12rem;
    line-height: .38rem;
    background-color: #fafafa;
}

.c-location-down-tips-close {
    padding-right: .12rem;
}

.c-location-down-tips-close:before {
    content: "";
    display: inline-block;
    width: 1px;
    height: .1rem;
    margin-right: .08rem;
    background-color: #ddd;
}

.c-location-down ul {
    list-style: none;
}

.c-location-down li {
    padding: .04rem .12rem;
    border-top: 1px solid #eee;
}


/*
 * navs组件样式
 * @author xielingjuan
 */

.c-navs {
    position: relative;
}

.c-navs-bar {
    position: relative;
    display: -webkit-box;
    display: -webkit-flex;
    min-width: 100%;
    height: 40px;
    white-space: nowrap;
    -webkit-align-content: flex-start;
    -webkit-align-items: stretch;
    -webkit-box-align: stretch;
    -webkit-box-direction: normal;
    -webkit-box-lines: single;
    -webkit-box-orient: horizontal;
    -webkit-box-pack: justify;
    -webkit-flex-direction: row;
    -webkit-flex-wrap: nowrap;
    -webkit-justify-content: space-between;
}

.c-navs .c-row-tile {
    border-bottom: 1px solid #f1f1f1;
}

.c-navs-sub .c-navs-bar {
    height: 38px;
}

.c-navs-bar * {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
}

.c-navs-bar-li {
    display: block;
    width: 16.66666667%;
    height: 40px;
    padding: 0 17px;
    font-size: 15px;
    line-height: 40px;
    text-align: center;
    text-decoration: none;
    list-style: none;
    color: #666;
    -webkit-box-flex: 1;
    -webkit-flex: 1 1 auto;
    -webkit-tap-highlight-color: transparent;
}

.c-navs-sub .c-navs-bar-li {
    height: 38px;
    line-height: 38px;
}

.c-navs-bar-li span {
    display: inline-block;
    overflow: hidden;
    max-width: 100%;
    height: 100%;
    white-space: nowrap;
    text-overflow: ellipsis;
}

.c-navs-bar .c-navs-bar-selected span {
    border-bottom: 2px solid #333;
    font-weight: 700;
    color: #333;
}

.c-navs-bar-view {
    position: relative;
    overflow: hidden;
}

.c-navs-bar-view .c-navs-bar {
    display: block;
}

.c-navs-bar-view .c-navs-bar .c-navs-bar-li {
    display: inline-block;
    width: auto;
    padding: 0 17px;
}

.c-navs-bar-toggle {
    position: absolute;
    top: 0;
    right: 0;
    width: 34px;
    height: 40px;
    background-color: #fff;
}

.c-navs-sub .c-navs-bar-toggle {
    height: 38px;
}

.c-navs-bar-toggle i {
    position: absolute;
    top: 17px;
    right: 17px;
    width: 0;
    height: 0;
    border-top: 5px solid #999;
    border-right: 5px solid transparent;
    border-left: 5px solid transparent;
}

.c-navs-bar-layer {
    position: absolute;
    z-index: 8;
    top: 0;
    overflow-x: hidden;
    width: 100%;
    background-color: #fff;
}

.c-navs-bar-layer p {
    padding: 9px 17px 13px;
    color: #999;
}

.c-navs-sub .c-navs-bar-layer p {
    padding: 8px 17px 13px;
}

.c-navs-bar-layer .c-row {
    margin-bottom: 17px;
}

.c-navs-sub .c-navs-bar-toggle i {
    top: 16px;
}

.c-navs-bar-layer .c-navs-bar-toggle i {
    border-top: none;
    border-right: 5px solid transparent;
    border-bottom: 5px solid #999;
    border-left: 5px solid transparent;
}

.c-navs-bar-layer .c-navs-bar-li {
    width: 33.33333333%;
    height: 33px;
    padding-right: 1.55367232%;
    padding-left: 1.55367232%;
    font-size: 14px;
    line-height: 33px;
    text-align: center;
    color: #333;
    -webkit-box-flex: 4;
    -webkit-flex: 4 4 auto;
}

.c-navs-bar-layer .c-span4.c-navs-bar-li span {
    display: inline-block;
    width: 100%;
    border: 1px solid #f1f1f1;
    border-bottom: 1px solid #f1f1f1;
}

.c-navs-bar-layer .c-span4.c-navs-bar-selected span {
    border: 2px solid #333;
    line-height: 31px;
}

.c-navs-shadow {
    position: absolute;
    top: 0;
    right: 34px;
    width: 10px;
    height: 40px;
    background: -webkit-linear-gradient(left, rgba(255, 255, 255, 0), #fff);
    background: linear-gradient(to right, rgba(255, 255, 255, 0), #fff);
}

.c-navs-sub .c-navs-shadow {
    height: 38px;
}

.c-navs-bar-mask {
    position: absolute;
    z-index: 7;
    top: 0;
    left: 0;
    width: 100%;
    height: 1024px;
    background: rgba(0, 0, 0, .65);
}

.c-navs-sub .c-navs-bar-li span {
    border-bottom: none;
    font-size: 14px;
}


